.footer {
  padding: 40px 0 0;
  background-color: $primary;

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    color: $white;
    margin-bottom: 16px;
  }

  &__slogan {
    border-top: 1px solid $border-color;
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: $white;
    padding: 15px 0;
  }

  &__end {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 10px 0;
    border-top: 1px solid $border-color;
  }

  &__copyright {
    color: $white;
  }

  @media (max-width: 991px) {
    &__slogan {
      padding: 10px 0;
      font-size: 16px;
    }

    &__copyright {
      margin-bottom: 8px;
    }
  }
}

.f-form {
  .form-group {
    margin-bottom: 6px;
  }

  .form-control {
    height: 30px;
    border: 0;
    border-radius: 0;
  }

  &__btn {
    border-radius: 0;
    border: 0;
    font-weight: 700;
    padding: 0 20px;
    line-height: 30px;

    &:focus {
      box-shadow: unset;
    }
  }
}

.f-contact {
  color: $white;
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    position: relative;
    padding-left: 24px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    & > .fa {
      position: absolute;
      top: 2px;
      left: 0;
    }

    a,
    a:hover {
      color: $white;
    }
  }
}

.f-social {
  display: flex;

  &__label {
    color: $white;
    margin-right: 12px;
    white-space: nowrap;
  }

  &__list {
    display: flex;
  }

  &__item {
    display: inline-block;
    color: $white;

    &:hover {
      color: $white;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 25px;
      position: relative;

      &::after {
        content: "";
        display: block;
        height: 16px;
        border: 1px solid $border-color;
        position: absolute;
        top: 50%;
        right: -13px;
        transform: translateY(-50%);
      }
    }
  }
}
