.section {
  & + & {
    margin-top: 20px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    color: $primary;
    text-transform: uppercase;
    padding-bottom: 4px;
    margin-bottom: 16px;
    border-bottom: 1px solid $border-color;
  }
}

.section-2 {
  &__title {
    font-size: 18px;
    font-weight: 700;
    color: $primary;
    text-transform: uppercase;
  }

  &__desc {
    margin-bottom: 16px;
  }

  &__filter {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 16px;

    &::before {
      content: "";
      display: block;
      flex: 1;
      border-top: 1px solid $border-color;
      margin-right: 10px;
    }
  }
}

.aside {
  padding: 16px;
  border: 1px solid $border-color;

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: $primary;
    margin-bottom: 16px;
  }
}
