.product {
  position: relative;

  &__frame {
    @include frame("rect", 100%);
    @include frame("zoom");
  }

  &__saleoff {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    font-size: 10px;
    font-weight: 700;
    text-align: center;
    color: $white;
    background-color: $danger;
    border-radius: 50%;
    position: absolute;
    z-index: 10;
    top: 10px;
    right: 10px;
  }

  &__title {
    color: $primary;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    margin: 12px 0 0;
  }

  &__price {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    font-size: 14px;
    font-weight: 700;
    color: $danger;
    margin-top: 6px;

    del {
      font-weight: 400;
      color: #666;
    }

    span,
    del {
      display: inline-block;
      margin: 0 2px;
      white-space: nowrap;
    }
  }
}

.news {
  &__frame {
    @include frame("rect", 66.66%);
    @include frame("zoom");
    width: 255px;
    margin-right: 24px;
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  &__info {
    margin-bottom: 10px;
  }

  &__desc {
    @include max-line(3);
    margin-bottom: 20px;
  }

  &__link {
    &,
    &:hover {
      color: $danger;
    }
  }

  @media (max-width: 767px) {
    &__frame {
      width: 160px;
      margin-right: 16px;
    }

    &__title {
      margin-bottom: 8px;
    }

    &__info {
      margin-bottom: 4px;
      color: #666;
    }

    &__desc {
      display: none;
    }
  }
}
