.menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &-item {
    margin: 4px 0 8px;
    position: relative;
  }

  &-link {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    padding: 0 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: inherit;

    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }

  &-toggle {
    display: inline-flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    span {
      display: inline-flex;
      width: 24px;
      height: 24px;
      justify-content: center;
      align-items: center;
      border-radius: 12px;
      transition: 0.3s;

      &:hover {
        background-color: rgba(#000, 0.1);
      }
    }

    &.active span {
      transform: rotate(90deg);
    }
  }

  &-sub {
    display: none;
  }

  &-sub &-link {
    padding-left: 40px;
  }

  &-sub-2 &-link {
    padding-left: 60px;
  }

  &-sub-3 &-link {
    padding-left: 80px;
  }

  &-item:hover > &-link {
    background-color: rgba($primary, 0.05);
  }

  &-mega-bg {
    display: none;
  }

  &-root > &-item > &-link {
    text-transform: uppercase;
    font-weight: 700;
  }

  @media (min-width: #{$navbar-breakpoint}px) {
    // menu root
    &-root {
      display: flex;
      justify-content: space-between;
    }

    &-root > &-item {
      margin: 0;
    }

    &-root > &-item > &-link {
      padding: 0;
      font-size: 16px;
      color: $primary;

      &.active {
        color: $danger;
      }
    }

    &-root > &-item-group > &-link {
      &::after {
        content: "\f0d7";
        font-family: "fontawesome";
        margin-left: 4px;
      }
    }

    &-root > &-item:hover > &-link {
      background: 0;
      color: $danger;
    }

    &-root > &-item-group > &-toggle {
      display: none;
    }

    &-root &-sub {
      padding: 6px 12px;
      min-width: 200px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
      position: absolute;
      z-index: 2;
      top: -6px;
      left: calc(100% + 4px);
      transform-origin: 0 4px;
      background-color: $primary;

      &::before {
        content: "";
        display: block;
        width: 8px;
        height: 100%;
        position: absolute;
        top: 0;
        right: 100%;
      }
    }

    &-root &-sub &-item {
      margin: 0;

      &:not(:last-child) {
        border-bottom: 1px solid $white;
      }
    }

    &-root &-sub &-link {
      color: $white;
      padding: 0;
      font-weight: 700;
      text-transform: uppercase;
      position: relative;
      left: 0;
      transition: 0.3s;
    }

    &-root &-sub &-item:hover > &-link {
      left: 5px;
    }

    &-root &-sub &-toggle {
      color: $white;
    }

    &-root &-sub &-item-group > &-link {
      padding-right: 40px;
    }

    &-root &-item-group:hover > &-sub {
      display: block;
      animation: menu-float-show 0.2s ease-out;
    }

    &-root > &-item-group > &-sub {
      transform-origin: 0 0;
      top: calc(100% + 8px);
      left: 0;

      &::before {
        width: 100%;
        height: 8px;
        right: auto;
        left: 0;
        top: auto;
        bottom: 100%;
      }
    }
  }
}

@keyframes menu-float-show {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.h-menu {
  &__btn {
    display: none;
  }

  @media (min-width: 1200px) {
    &__btn {
      display: inline-block;
      font-size: 13px;
      font-weight: 700;
      text-transform: uppercase;
      cursor: pointer;
      position: relative;
      z-index: 1;
      line-height: 55px;
      padding: 0 20px 0 10px;

      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 20px;
        position: absolute;
        top: 100%;
        left: 0;
      }
    }

    &__float {
      display: none;
      width: 246px;
      background-color: $white;
      position: absolute;
      z-index: 200;
      top: 100%;
      left: 0;
    }

    &__backdrop {
      display: none;
      width: 100vw;
      height: 200vh;
      position: absolute;
      top: 100%;
      left: 50%;
      right: 0;
      z-index: 100;
      transform: translateX(-50%);
      background-color: rgba(#000, 0.5);
    }

    &__content:hover &__float {
      display: block;
    }

    &__content:hover ~ &__backdrop {
      display: block;
    }
  }
}

body.is-home .h-menu__float {
  display: block;
}

body.is-home .h-menu__backdrop {
  display: none !important;
}
