.header {
  position: relative;
  z-index: 100;
}

.navigation {
  position: relative;
  z-index: 100;

  &__inner {
    padding: 8px 0;
  }

  &__btn {
    &:focus {
      outline: 0;
      box-shadow: unset;
    }
  }

  @media (min-width: 1200px) {
    &__btn {
      display: none;
    }
  }
}
