.banner-slider {
  position: relative;
  z-index: 1;

  &__prev,
  &__next {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    font-size: 40px;
    color: $primary;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    cursor: pointer;

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: 0;
  }

  &__pagination {
    position: absolute;
    z-index: 10;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);

    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      border-radius: 50%;
      border: 1px solid $white;
      opacity: 1;
      margin: 0 3px;
      background: 0;

      &-active {
        border-color: $primary;
        background: $primary;
      }
    }
  }

  @media (max-width: 767px) {
    &__prev,
    &__next {
      padding: 10px;
      font-size: 30px;
    }

    &__pagination {
      bottom: 4px;

      .swiper-pagination-bullet {
        width: 8px;
        height: 8px;
      }
    }
  }
}

.product-slider {
  position: relative;

  &__prev,
  &__next {
    display: flex;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    color: $white;
    background-color: $primary;
    border-radius: 50%;
    font-weight: 700;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: -30px;
    transform: translateY(-50%);
    cursor: pointer;

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: -30px;
  }

  @media (max-width: 575px) {
    &__prev {
      left: -12px;
    }

    &__next {
      right: -12px;
    }
  }
}
