.pagination {
  margin-bottom: 0;

  .page-item {
    &:not(:last-child) {
      margin-right: 4px;
    }
  }

  .page-link {
    color: $gray-800;
    padding: 8px 16px;
    border-radius: 4px;
  }
}

.breadcrumb {
  padding: 15px 0;

  &-item {
    &,
    & + &::before,
    &.active,
    &.active::before,
    a,
    a:hover {
      color: $gray-800;
    }
  }
}

$phoneRingColor: #e73a35;
.phone-ring {
  display: flex;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 50%;
  background-color: $phoneRingColor;
  position: fixed;
  z-index: 100;
  bottom: 50px;
  left: 50px;
  box-shadow: 0 5px 20px rgba(#000, 0.3);

  &:hover {
    color: $white;
  }

  &::before {
    content: "";
    display: block;
    width: 250%;
    height: 250%;
    border-radius: 50%;
    border: 1px solid $phoneRingColor;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: phone-circle-animate 1s ease-in-out infinite;
  }

  &::after {
    content: "";
    display: block;
    width: 160%;
    height: 160%;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: -1;
    transform: translate(-50%, -50%);
    background-color: rgba($phoneRingColor, 0.5);
    animation: phone-circle-fill-animate 2s ease-in-out infinite;
  }

  &__icon {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: rgba($phoneRingColor, 0.7);
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    animation: phone-icon-animate 1s ease-in-out infinite;
  }

  &__text {
    display: inline-block;
    background-color: rgba($phoneRingColor, 0.7);
    font-size: 14px;
    font-weight: 700;
    line-height: 40px;
    padding: 0 20px 0 60px;
    color: $white;
    border-radius: 20px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    box-shadow: 0 5px 20px rgba(#000, 0.3);
  }

  &:hover &__text {
    color: $phoneRingColor;
    background-color: rgba(#fff, 0.7);
  }

  @media (max-width: 767px) {
    left: 20px;
    bottom: 20px;

    &__text {
      display: none;
    }
  }
}

@keyframes phone-circle-animate {
  0% {
    transform: translate(-50%, -50%) rotate(0) scale(0.5);
    opacity: 0.1;
  }
  30% {
    transform: translate(-50%, -50%) rotate(0) scale(0.7);
    opacity: 0.5;
  }
  100% {
    transform: translate(-50%, -50%) rotate(0) scale(1);
    opacity: 0.1;
  }
}

@keyframes phone-circle-fill-animate {
  0%,
  100% {
    transform: translate(-50%, -50%) rotate(0) scale(0.7);
    opacity: 0.2;
  }
  50% {
    transform: translate(-50%, -50%) rotate(0) scale(1);
    opacity: 0.2;
  }
}

@keyframes phone-icon-animate {
  0%,
  100% {
    transform: rotate(0) scale(1) skew(1deg);
  }
  10% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }

  20%,
  40% {
    transform: rotate(25deg) scale(1) skew(1deg);
  }
  30% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }
  50% {
    transform: rotate(0) scale(1) skew(1deg);
  }
}

.as-menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__link {
    display: block;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    color: $gray-800;
    padding: 8px 0;
    transition: 0.3s;
    position: relative;
    left: 0;

    &:hover {
      color: $primary;
      text-decoration: none;
      left: 5px;
    }
  }

  &__sub {
    list-style: none;
    margin: 0;
    padding: 0;
    border-bottom: 1px dotted $border-color;
  }

  &__sub-item {
    border-top: 1px dotted $border-color;
  }

  &__sub-link {
    display: block;
    font-size: 14px;
    padding: 8px 14px;
    color: $gray-800;
    text-transform: uppercase;
    transition: 0.3s;
    position: relative;
    left: 0;

    &:hover {
      color: $primary;
      text-decoration: none;
      left: 5px;
    }
  }
}

.pd-info {
  &__title {
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 12px;
  }

  &__price {
    font-size: 16px;
    font-weight: 700;
    color: $danger;
  }

  &__desc {
    margin-top: 8px;
  }

  &__footer {
    align-items: center;
  }

  &__btn {
    display: inline-block;
    color: $white;
    line-height: 40px;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 20px;
    padding: 0 40px;
    border: 0;
    background-color: $danger;
    margin-right: 10px;

    &:hover {
      color: $white;
      text-decoration: none;
      background-color: darken($danger, 10%);
    }
  }

  &__hotline {
    .fa {
      color: $danger;
    }

    a,
    a:hover {
      color: $danger;
      font-weight: 700;
    }
  }

  @media (max-width: 575px) {
    &__btn {
      padding: 0 20px;
      line-height: 40px;
      font-size: 16px;
    }
  }
}

.pd-detail {
  &__header {
    display: flex;
    border-bottom: 1px solid $border-color;
    margin-bottom: 20px;
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    color: $white;
    text-transform: uppercase;
    padding: 10px 20px;
    background-color: $primary;
    margin-bottom: 0;
  }
}

.post {
  &-title {
    font-size: 22px;
    font-weight: 700;
    color: $primary;
    margin-bottom: 10px;
  }

  &-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #999;
    margin-bottom: 10px;
  }

  &-sapo {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 1rem;
  }
}

.relate-news {
  padding-left: 17px;
  margin: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 6px;
    }
  }

  &__link {
    color: #777;
  }
}

.contact-map {
  height: 400px;
}

.ct-form {
  max-width: 750px;
  margin: 0 auto;
  padding: 45px 0;

  &__title {
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 12px;
  }

  .form-control {
    border-radius: 0;
  }

  &__btn {
    display: inline-block;
    line-height: 40px;
    padding: 0 30px;
    font-size: 18px;
    font-weight: 700;
    color: $white;
    text-transform: uppercase;
    border: 0;
    background-color: $danger;
    &:hover {
      color: $white;
      background-color: darken($danger, 5%);
    }
  }
}
